        glGame.emitter.on(CONST.clientEvent.bossGodOfWealthCoin,this.bossGodOfWealthCoinHandler,this);  //
        glGame.emitter.on(CONST.clientEvent.bossLavaBasaltComin,this.bossLavaBasaltCominHandler,this);  //
        glGame.emitter.on(CONST.clientEvent.checkSpecialCannon,this.checkSpecialCannonHandler,this);   //
